    min-height: 340px;
    height: auto;
    margin: 0;
  }
}
@media (max-width: 767.5px) {
  .contacts-map__static-map img {
    min-height: 320px;
  }
}

.contacts-map__interactive-map {
  display: block;
  grid-column: 1/2;
  grid-row: 1/2;
  min-width: 585px;
  width: 100%;
  min-height: 324px;
  margin: 0 auto;
  margin-top: 18px;
}
